
.dropdown {
  display: inline-block;
  position: relative;
  vertical-align: middle;
}
.dropdown .button {
    padding: 0 1rem;
    height: 2.125rem;
    text-align: center;
    vertical-align: middle;
    background-color: #ffffff;
    border: 1px #d9d9d9 solid;
    color: #262626;
    cursor: pointer;
    display: inline-block;
    outline: none;
    font-size: .875rem;
    margin: .15625rem 0;
    position: relative;
}
.dropdown .button.dropdown-toggle {
      padding-right: 1.625rem;
}
.dropdown .button.dropdown-toggle:before {
        display: block;
        position: absolute;
        vertical-align: middle;
        color: transparent;
        font-size: 0;
        content: "";
        height: 5px;
        width: 5px;
        background-color: transparent;
        border-left: 1px solid;
        border-bottom: 1px solid;
        border-color: #1d1d1d;
        top: 50%;
        left: 100%;
        margin-left: -1rem;
        margin-top: -0.1625rem;
        z-index: 2;
        -webkit-transform: rotate(-45deg);
        transform: rotate(-45deg);
        transition: all 0.3s ease;
}
.dropdown .button.dropdown-toggle.active-toggle:before {
        -webkit-transform: rotate(135deg);
        transform: rotate(135deg);
        transition: all 0.3s ease;
}
.dropdown .dropdown-menu {
    margin: 0;
    padding: 0;
    width: auto;
    text-align: left;
    background: #ffffff;
    max-width: 15.625rem;
    list-style: none inside none;
    border-collapse: separate;
    position: absolute;
    z-index: 1000;
    left: 0;
    box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.2);
}
.dropdown .dropdown-menu li {
      display: block;
      float: none;
      position: relative;
      line-height: 1.25rem;
}
.dropdown .dropdown-menu li:hover a:not(.disabled) {
        color: white;
        background: #59cde2;
}
.dropdown .dropdown-menu li a {
        color: #727272;
        font-size: .875rem;
        display: block;
        float: none;
        padding: .5rem 1.5rem;
        text-decoration: none;
        vertical-align: middle;
        position: relative;
        min-width: 12.5rem;
        border: none;
        cursor: pointer;
        word-break: break-all;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
}
.dropdown .dropdown-menu li a.disabled {
          color: #999;
          background: #eee;
          cursor: not-allowed;
}
.dropdown .dropdown-menu.dropdown-menu-right {
      right: 0;
      left: auto;
}



