
.vi-alert .cover-mask {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  background: rgba(29, 29, 29, 0.7);
  z-index: 100;
}
.vi-alert .alert-panel {
  padding: 0.825rem 0.5rem;
  position: absolute;
  left: 50%;
  top: 50%;
  background: #fff;
  width: 420px;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  border-radius: 4px;
  z-index: 101;
}
.vi-alert .alert-panel .alert-header {
    padding: 0.5rem;
    padding-bottom: 0;
}
.vi-alert .alert-panel .alert-header h4 {
      margin: 0;
}
.vi-alert .alert-panel .alert-header h4 span {
        margin-top: -0.5rem;
        float: right;
        cursor: pointer;
        font-size: 1.5rem;
        color: #666;
        -webkit-transform: rotate(45deg);
                transform: rotate(45deg);
}
.vi-alert .alert-panel .alert-header h4 span:hover {
          color: #1b89de;
}
.vi-alert .alert-panel .alert-content {
    padding: 1rem 0.5rem;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
}
.vi-alert .alert-panel .alert-content .icon, .vi-alert .alert-panel .alert-content .main {
      -ms-flex-item-align: center;
          -ms-grid-row-align: center;
          align-self: center;
}
.vi-alert .alert-panel .alert-content .icon {
      -webkit-box-flex: 0;
          -ms-flex: 0 0 auto;
              flex: 0 0 auto;
      padding-right: 0.75rem;
}
.vi-alert .alert-panel .alert-content .icon > svg {
        vertical-align: middle;
}
.vi-alert .alert-panel .alert-content .main {
      -webkit-box-flex: 1;
          -ms-flex: 1 1 auto;
              flex: 1 1 auto;
}
.vi-alert .alert-panel .alert-footer {
    overflow: auto;
    _height: 1%;
}
.vi-alert .alert-panel .alert-footer .alert-footer-btns {
      padding: 0px 0.5rem;
      float: right;
}
.vi-alert .alert-panel .alert-footer .alert-footer-btns button {
        padding: 0 1rem;
        line-height: 2.125rem;
        border: none;
        cursor: pointer;
        color: #262626;
        background: #fff;
        border: 1px solid #d9d9d9;
        border-radius: 3px;
}
.vi-alert .alert-panel .alert-footer .alert-footer-btns button:active {
          background: #eee;
}
.vi-alert .alert-panel .alert-footer .alert-footer-btns button:focus {
          outline: none;
}



