
.vi-select {
  position: relative;
  display: inline-block;
}
.vi-select .vi-select-input {
    margin: .325rem 0;
    height: 2.125rem;
    min-height: 2.125rem;
}
.vi-select .vi-select-input input {
      -webkit-appearance: none;
         -moz-appearance: none;
              appearance: none;
      padding: .375rem;
      padding-right: 1.4rem;
      position: relative;
      border: 1px #d9d9d9 solid;
      width: 100%;
      height: 100%;
      font-size: 0.875rem;
      z-index: 0;
      cursor: pointer;
}
.vi-select .vi-select-input .arrow-down {
      display: block;
      position: absolute;
      top: 19px;
      border-top: 4px solid #888;
      border-left: 4px solid transparent;
      border-right: 4px solid transparent;
}
.vi-select .dropdown-menu {
    margin: 2px 0 0 0;
    padding: 0;
    width: auto;
    text-align: left;
    background: #ffffff;
    max-height: 20rem;
    overflow: auto;
    list-style: none inside none;
    border-collapse: separate;
    position: absolute;
    z-index: 1000;
    left: 0;
    box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.2);
}
.vi-select .dropdown-menu li {
      display: block;
      float: none;
      position: relative;
      line-height: 1.25rem;
}
.vi-select .dropdown-menu li:hover a:not(.disabled) {
        color: white;
        background: #59cde2;
}
.vi-select .dropdown-menu li a {
        color: #727272;
        font-size: .875rem;
        display: block;
        float: none;
        padding: .4rem 1.5rem;
        text-decoration: none;
        vertical-align: middle;
        position: relative;
        white-space: nowrap;
        min-width: 12.5rem;
        border: none;
        cursor: pointer;
        word-break: break-all;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
}
.vi-select .dropdown-menu li a.disabled {
          color: #999;
          background: #eee;
          cursor: not-allowed;
}



