
.vi-pagination {
  text-align: center;
  font-size: 0;
}
.vi-pagination button {
    padding: 5px 10px;
    color: #666;
    background: #fff;
    border: 1px solid #d9d9d9;
    outline: none;
    cursor: pointer;
    font-size: 0.875rem;
}
.vi-pagination button:hover {
      color: #007ACC;
}
.vi-pagination .pagination-btn-group {
    display: inline;
}
.vi-pagination .pagination-btn-group button.active {
      color: #fff;
      background: #007ACC;
      border: 1px solid #007abb;
}
.vi-pagination .pagination-btn-group button:not(:last-child) {
      border-right: none;
}
.vi-pagination .pagination-pre:first-child {
    border-right: none;
}
.vi-pagination .pagination-total-show {
    display: inline-block;
    vertical-align: middle;
    padding-left: 1.75rem;
    line-height: 2.2rem;
    font-size: 0.875rem;
    color: #666;
}
.vi-pagination .ellipsis {
    border-left: none;
    border-right: none;
}



