
.vi-tabs > ul {
  margin: 0;
  padding: 0;
  list-style: none;
  overflow: auto;
  _height: 1%;
  border-bottom: 2px solid #1ba1e2;
}
.vi-tabs > ul li {
    float: left;
    padding: 0.5rem 1.2rem;
    cursor: pointer;
}
.vi-tabs > ul li.active {
      color: #fff;
      background: #1ba1e2;
}
.vi-tabs > ul li:hover:not(.active) {
      background: #eee;
}



