
.vue-datepicker .vue-datepicker-input {
  position: relative;
  height: 2.125rem;
  margin: .325rem 0;
}
.vue-datepicker .vue-datepicker-input input {
    -webkit-appearance: none;
       -moz-appearance: none;
            appearance: none;
    padding: .3125rem;
    padding-right: 1.5rem;
    position: relative;
    border: 1px #d9d9d9 solid;
    width: 100%;
    height: 100%;
    font-size: 0.875rem;
    z-index: 0;
}
.vue-datepicker .vue-datepicker-input input:hover {
      border-color: #787878;
}
.vue-datepicker .vue-datepicker-input input:focus {
      outline: none;
      border-color: #1ba1e2;
}
.vue-datepicker .vue-datepicker-input svg {
    position: absolute;
    right: 5px;
    top: 6px;
}
.vue-datepicker .vue-datepicker-wrap {
  width: 240px;
  position: absolute;
  padding-bottom: 1rem;
  background: #fff;
  z-index: 999;
  box-shadow: 2px 2px 8px #bdb8b8;
}
.vue-datepicker .vue-datepicker-wrap .vue-datepicker-header {
    padding: 0px 15px;
    font-size: 14px;
    text-align: center;
    line-height: 36px;
    border-bottom: 1px solid #ccc;
}
.vue-datepicker .vue-datepicker-wrap .vue-datepicker-header .vue-datepicker-header-btn {
      cursor: pointer;
}
.vue-datepicker .vue-datepicker-wrap .vue-datepicker-header .vue-datepicker-header-btn:hover {
        color: #0288d1;
}
.vue-datepicker .vue-datepicker-wrap .vue-datepicker-header .vue-datepicker-header-btn-pre {
      float: left;
}
.vue-datepicker .vue-datepicker-wrap .vue-datepicker-header .vue-datepicker-header-btn-next {
      float: right;
}
.vue-datepicker .vue-datepicker-wrap .vue-datepicker-content {
    position: relative;
}
.vue-datepicker .vue-datepicker-wrap .vue-datepicker-content table {
      width: 100%;
      border-collapse: collapse;
}
.vue-datepicker .vue-datepicker-wrap .vue-datepicker-content table thead {
        line-height: 2rem;
        font-size: 0.75rem;
        color: #757575;
        border-bottom: 1px solid #ccc;
}
.vue-datepicker .vue-datepicker-wrap .vue-datepicker-content table tbody tr td {
        font-size: 0.75rem;
        padding: 10px 0;
        text-align: center;
        cursor: pointer;
}
.vue-datepicker .vue-datepicker-wrap .vue-datepicker-content table tbody tr td.active, .vue-datepicker .vue-datepicker-wrap .vue-datepicker-content table tbody tr td.active:hover {
          color: #fff;
          background: #0288d1;
}
.vue-datepicker .vue-datepicker-wrap .vue-datepicker-content table tbody tr td.flag {
          color: #bdbdbd !important;
}
.vue-datepicker .vue-datepicker-wrap .vue-datepicker-content table tbody tr td:hover {
        background: #eee;
}
.vue-datepicker .vue-datepicker-wrap .vue-datepicker-content .vue-date-picker-year-panel {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: #fff;
}
.vue-datepicker .vue-datepicker-wrap .vue-datepicker-content .vue-date-picker-year-panel > div {
        width: 50%;
        height: 100%;
        display: inline-block;
        float: left;
        overflow: hidden;
}
.vue-datepicker .vue-datepicker-wrap .vue-datepicker-content .vue-date-picker-year-panel > div ul {
          height: 100%;
          margin: 0;
          padding-left: 0;
          box-sizing: border-box;
          overflow-y: auto;
          list-style: none;
}
.vue-datepicker .vue-datepicker-wrap .vue-datepicker-content .vue-date-picker-year-panel > div ul li {
            font-size: 14px;
            text-align: center;
            line-height: 30px;
            cursor: pointer;
}
.vue-datepicker .vue-datepicker-wrap .vue-datepicker-content .vue-date-picker-year-panel > div ul li.active {
              color: #fff;
              background: #ccc;
}
.vue-datepicker .vue-datepicker-wrap .vue-datepicker-content .vue-date-picker-year-panel > div:first-child {
          border-right: 1px solid #ccc;
}



