
.vi-upload {
  display: inline-block;
  min-height: 2.125rem;
  height: 2.125rem;
  width: 100%;
  position: relative;
  vertical-align: middle;
  margin: .325rem 0;
  line-height: 1;
}
.vi-upload input[type='file'] {
    position: absolute;
    opacity: 0;
    width: 0.0625rem;
    height: 0.0625rem;
}
.vi-upload input[type='text'] {
    -webkit-appearance: none;
       -moz-appearance: none;
            appearance: none;
    padding: .3125rem;
    position: relative;
    border: 1px #d9d9d9 solid;
    width: 100%;
    height: 100%;
    font-size: 0.875rem;
    z-index: 0;
    cursor: pointer;
}
.vi-upload button {
    position: absolute;
    top: 0;
    right: 0;
    z-index: 2;
    margin: 0;
    padding: 0 1rem;
    height: 2.125rem;
    text-align: center;
    vertical-align: middle;
    background-color: #ffffff;
    border: 1px #d9d9d9 solid;
    color: #262626;
    cursor: pointer;
    display: inline-block;
    outline: none;
    font-size: .875rem;
}
.vi-upload:hover input, .vi-upload:hover button {
    border-color: #787878;
}



