
.vi-confirm .cover-mask {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  background: rgba(29, 29, 29, 0.7);
  z-index: 100;
}
.vi-confirm .confirm-panel {
  padding: 0.825rem 0.5rem;
  position: absolute;
  left: 50%;
  top: 50%;
  background: #fff;
  width: 420px;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  border-radius: 4px;
  z-index: 101;
}
.vi-confirm .confirm-panel .confirm-header {
    padding: 0.5rem;
    padding-bottom: 0;
}
.vi-confirm .confirm-panel .confirm-header h4 {
      margin: 0;
}
.vi-confirm .confirm-panel .confirm-header h4 span {
        margin-top: -0.5rem;
        float: right;
        cursor: pointer;
        font-size: 1.5rem;
        color: #666;
        -webkit-transform: rotate(45deg);
                transform: rotate(45deg);
}
.vi-confirm .confirm-panel .confirm-header h4 span:hover {
          color: #1b89de;
}
.vi-confirm .confirm-panel .confirm-content {
    padding: 1rem 0.5rem;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
}
.vi-confirm .confirm-panel .confirm-content .icon, .vi-confirm .confirm-panel .confirm-content .main {
      -ms-flex-item-align: center;
          -ms-grid-row-align: center;
          align-self: center;
}
.vi-confirm .confirm-panel .confirm-content .icon {
      -webkit-box-flex: 0;
          -ms-flex: 0 0 auto;
              flex: 0 0 auto;
      padding-right: 0.75rem;
}
.vi-confirm .confirm-panel .confirm-content .icon > svg {
        vertical-align: middle;
}
.vi-confirm .confirm-panel .confirm-content .main {
      -webkit-box-flex: 1;
          -ms-flex: 1 1 auto;
              flex: 1 1 auto;
}
.vi-confirm .confirm-panel .confirm-footer {
    overflow: auto;
    _height: 1%;
}
.vi-confirm .confirm-panel .confirm-footer .confirm-footer-btns {
      padding: 0px 0.5rem;
      float: right;
}
.vi-confirm .confirm-panel .confirm-footer .confirm-footer-btns button {
        padding: 0 1rem;
        line-height: 2.125rem;
        border: none;
        cursor: pointer;
        border-radius: 3px;
}
.vi-confirm .confirm-panel .confirm-footer .confirm-footer-btns button.confirm-footer-btn-confirm {
          color: #fff;
          background: #1b89de;
}
.vi-confirm .confirm-panel .confirm-footer .confirm-footer-btns button.confirm-footer-btn-confirm:active {
            background: #1b6eae;
}
.vi-confirm .confirm-panel .confirm-footer .confirm-footer-btns button.confirm-footer-btn-cancel {
          color: #262626;
          background: #fff;
          border: 1px solid #d9d9d9;
}
.vi-confirm .confirm-panel .confirm-footer .confirm-footer-btns button:focus {
          outline: none;
}



