
.vi-slide {
  width: 100%;
  height: 100%;
  position: relative;
  overflow: hidden;
}
.vi-slide ul {
    margin: 0;
    padding: 0;
    height: 100%;
    position: absolute;
    list-style: none;
    font-size: 0;
    transition: all .5s ease;
}
.vi-slide ul li {
      height: 100%;
      float: left;
}
.vi-slide ul li a {
        width: 100%;
        height: 100%;
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center;
}
.vi-slide ul:after {
      content: '';
      display: block;
      clear: both;
      visibility: hidden;
      line-height: 0;
      height: 0;
      font-size: 0;
}
.vi-slide .vi-slide-btns {
    position: absolute;
    height: auto;
    bottom: 2rem;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
}
.vi-slide .vi-slide-btns li span {
      display: inline-block;
      width: 1rem;
      height: 1rem;
      margin: 6px;
      border-radius: 100%;
      background: #999;
      cursor: pointer;
}
.vi-slide .vi-slide-btns li span.active {
        background: #fff;
}
.vi-slide .vi-slide-arrow {
    display: none;
    position: absolute;
    padding: 0.5rem;
    top: 50%;
    border: none;
    cursor: pointer;
    background: rgba(0, 0, 0, 0.3);
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
}
.vi-slide .vi-slide-arrow:hover {
      background: rgba(0, 0, 0, 0.6);
}
.vi-slide .vi-slide-arrow:focus {
      outline: none;
}
.vi-slide .vi-slide-arrow.vi-slide-arrow-left {
      left: 2rem;
}
.vi-slide .vi-slide-arrow.vi-slide-arrow-right {
      right: 2rem;
}
.vi-slide:hover .vi-slide-arrow {
    display: inline-block;
}



